import React, {useEffect, useRef, useState} from 'react';
import styled from 'styled-components';

const MBVPDiv = styled.div`
  position: relative;
  height: 100%;
  width: 90%;
  left: 5%;
  overflow: scroll;
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  box-sizing: border-box;
  padding: ${props => props.paddingPx}px 0px;
  

  mask-image: linear-gradient(to bottom, transparent 2%, black 4%, black 96%, transparent 98%);
  
  ::-webkit-scrollbar {
    display: none;
  }
`

const BIBackPanel = styled.div`
  position: relative;
  margin: 1rem;
  width: ${props => props.layout?.w || '10rem'};
  height: ${props => props.layout?.h || '15rem'};
  background: #aaaaaaa0;
  border-radius: 0.5rem;
  box-shadow: inset 0.2rem 0.2rem 4px #00000040, inset -0.2rem -0.2rem 4px #ffffff60;
`
const BIPanel = styled.div`
  position: absolute;
  top: 2.5%;
  left: 2.5%;
  height: 95%;
  width: 95%;
  border-radius: 0.2rem;
  background: ${props => props.layout?.bgi? `url("${props.layout.bgi}")` : '#f6f6f6d0'};
  transition: 0.2s;

  :hover {
    top: -8%;
    left: -5%;
    width: 110%;
    height: 110%;
    border-radius: 0.5rem;
    background: #e7ded8;
    box-shadow: 0.5rem 1.2rem 10px #423d3d80;
  }
`

function BIconPanel({layout, content}) {
    return(
        <BIBackPanel layout={layout}>
            <BIPanel>

            </BIPanel>
        </BIBackPanel>
    )
}



